/* COMIENZO DE ESTILOS PARA NAV */
.nav {
    background: #411010;
    width: 100%;
    height: 100px;
    color: #ffffff;
    z-index: 100;
}

.nav_container {
    display: flex;
    height: 100%;
    width: 90%;
    margin: 0 auto;
    justify-content: space-between;
    align-items: center;
}

.nav_logo {
    font-weight: 900;
    font-size: 40px;
}

.nav_menu {
    display: grid;
    grid-auto-flow: column;
    gap: 3em;
}

.nav_item {
    color: #ffffff;
    text-decoration: none;
    --clippy: polygon(0 0, 0 0, 0 100%, 0% 100%);

    ::after {
        content: "";
        display: block;
        background: #fff;
        width: 90%;
        margin-top: 3px;
        height: 3px;
        clip-path: var(--clippy);
        transition: clip-path 0.3s;
    }

    :hover {
        --clippy: polygon(0 0, 100% 0, 100% 100%, 0 100%);
    }
}

.nav_item1 {
    color: #ffffff;
    text-decoration: none;
    --clippy: polygon(0 0, 0 0, 0 100%, 0% 100%);

    ::after {
        content: "";
        display: block;
        background: #fff;
        width: 90%;
        margin-top: 3px;
        height: 3px;
        clip-path: var(--clippy);
        transition: clip-path 0.3s;
    }
}

.nav_item2 {
    color: #ffffff;
    text-decoration: none;
    --clippy: polygon(0 0, 0 0, 0 100%, 0% 100%);

    ::after {
        content: "";
        display: block;
        background: #fff;
        width: 90%;
        margin-top: 3px;
        margin: 30px;
        height: 3px;
        clip-path: var(--clippy);
        transition: clip-path 0.3s;
    }

}

.nav_item1:hover {
    --clippy: polygon(0 0, 100% 0, 100% 100%, 0 100%);
}

.nav_input:checked+.nav_menu {
    background: #411010;
}

.nav_label,
.nav_input {
    display: none;
}

