/* 	Product details  */
.product-details {
    position: relative;
    text-align: left;
    overflow: hidden;
    padding: 30px;
    height: 100%;
    float: left;
    width: 50%;
}

/* product image  */
.product-image {
    transition: all 0.3s ease-out;
    display: inline-block;
    position: relative;
    overflow: hidden;
    height: 100%;
    float: right;
    width: 50%;
    display: inline-block;

    :hover .info {
        transform: translateX(0);
    }

    :hover img {
        transition: all 0.3s ease-out;
        transform: scale(1.2, 1.2);
    }
}

.info ul li {
    transition: 0.3s ease;

    :hover {
        transform: translateX(50px) scale(1.3);
    }
}

